<template>
  <div
    :class="
      danger != ''
        ? 'CameDrag border overflow-auto danger'
        : 'CameDrag border overflow-auto'
    "
    @dblclick="toggle_cam(null)"
    @mouseenter="toggle_cam(false)"
    @mouseleave="toggle_cam(true)"
  >
    <div class="w-100 justify-content-center align-items-center d-flex">
      <div v-if="show_cam">
        <div class="w3-card-4 w3-padding">
          <div
            class="w3-display-container w3-center"
            :class="{
              'w3-opacity': !connecting,
            }"
          >
            <img class="w-100 h-100" :src="img" alt="" />
            <div v-if="!connecting" class="w3-display-middle w3-xlarge">
              <i class="fas fa-video-slash"></i>
            </div>
            <template v-else>
              <span
                v-if="signal != ''"
                class="w3-padding w3-display-middle text-danger"
              >
                {{ signal }}
                <i class="fa fa-spinner w3-spin"></i>
              </span>
              <div class="w3-padding w3-display-topleft text-danger">
                <i class="fas fa-video"></i>
              </div>
            </template>
          </div>
        </div>
      </div>
      <div class="w-100" v-else>
        <div class="w-100 text-left">
          <span>描述</span><br />
          <textarea
            class="border-0 w-100"
            rows="1"
            readonly
            v-model="cam.description"
          >
          </textarea>
        </div>
        <div class="w-100 text-right">
          <span @click="setting_fn(this)" class="text-success pointer mr-2">
            <i class="fa-lg fas fa-edit"></i>
          </span>
          <span @click="alarm_fn(this)" class="button pointer">關閉警報</span>
        </div>
        <div class="w-100 text-right">
          <div class="custom-control custom-switch">
            <input
              type="checkbox"
              class="custom-control-input"
              :id="`${cam.camera_id}-ai-switch`"
              v-model="cam.fence_enable"
              @change="open_ai_confirm"
            />
            <label
              class="custom-control-label"
              :for="`${cam.camera_id}-ai-switch`"
              >AI偵測</label
            >
          </div>
          <Confirm
            v-if="show_ai_confirm"
            :callback_fn="take_username()"
            :close_fn="close_ai_confirm"
          ></Confirm>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Confirm from "@/components/Confirm.vue";
import { CamUpdateFenceDetect, CamInfo } from "@/api/camera_detail";

// 顯示「點擊格子前」的鏡頭影像
export default {
  name: "CameDrag",
  props: {
    id: Number,
    data: {},
    setting_fn: {
      type: Function,
      default: function () {},
    },
    alarm_fn: {
      type: Function,
      default: function () {},
    },
    // drag_fn: {
    //   type: Function,
    //   default: function () {},
    // },
    // drag_start_fn: Function,
    // drop_fn: Function,
  },
  components: {
    Confirm,
  },
  data() {
    return {
      show_cam: true,
      ws: null,
      send_ws: function () {},
      close_ws: function () {},
      img: "",
      connecting: false,
      signal: "",
      danger: "",
      show_ai_confirm: false,
      cam: {},
    };
  },
  methods: {
    IsPC() {
      var userAgentInfo = navigator.userAgent;
      var Agents = [
        "Android",
        "iPhone",
        "SymbianOS",
        "Windows Phone",
        "iPad",
        "iPod",
      ];
      var flag = true;
      for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
          flag = false;
          break;
        }
      }
      return flag;
    },

    get_img() {
      return this.img != ""
        ? `background: url('data:image/png;base64,${this.img}') center center /cover`
        : "";
    },
    toggle_cam(v) {
      if (v == null) {
        this.show_cam = !this.show_cam;
      } else {
        if (!this.IsPC()) this.show_cam = v;
      }
      //!this.show_cam;
    },

    close_alarm() {
      console.log("關閉警報");
    },

    open_ai_confirm() {
      this.show_ai_confirm = true;
    },

    close_ai_confirm() {
      CamInfo(this.cam.camera_id)
        .then((res) => {
          if (res.status != 200) {
            window.alert(`系統異常: ${res.statusText}`);
            return;
          }
          return res.json();
        })
        .then((res) => {
          if (res.error_code !== 0) {
            window.alert(`${res.error_text} (${res.error_code})`);
            return;
          }

          this.cam = res.data;
        })
        .catch((err) => window.alert("網頁異常", err))
        .finally(() => (this.show_ai_confirm = false));
    },

    take_username() {
      const vm = this;
      return function (username, password) {
        if (vm.show_ai_confirm === false) {
          return;
        }

        CamUpdateFenceDetect(
          vm.cam.camera_id,
          vm.cam.fence_enable,
          username,
          password
        )
          .then((res) => {
            if (res.status != 200) {
              window.alert(`系統異常: ${res.statusText}`);
              return;
            }
            return res.json();
          })
          .then((res) => {
            if (res.error_code !== 0) {
              if (res.error_code == 8000) {
                this.hint = "帳號密碼錯誤";
              } else {
                this.hint = `帳號密碼錯誤: ${res.error_text}(${res.error_code})`;
              }
              return;
            }
            this.origin_ai_enable = vm.ai_enable;
            vm.close_ai_confirm(true);
          })
          .catch((err) => window.alert("網頁異常", err));
      };
    },
  },
  mounted() {
    this.cam = { ...this.data };
    const protocol = window.location.protocol === "https:" ? "wss" : "ws";
    this.ws = new WebSocket(
      `${protocol}://${window.location.host}/ws/camera/streaming/${this.cam.camera_id}`
    );
    this.ws.onopen = () => {
      this.connecting = true;
      this.danger = "";
      this.warning = "";
      this.close_ws = () => this.ws.close();
      this.send_ws = (payload) => this.ws.send(JSON.stringify(payload));
    };
    this.ws.onerror = () => {
      this.warning = "連線失敗";
    };
    this.ws.onclose = () => {
      this.connecting = false;
      this.close_ws = function () {};
      this.send_ws = function () {};
    };
    this.ws.onmessage = (e) => {
      const data = JSON.parse(e.data);
      if (data.error_code !== 0) {
        this.warning = data.error_text;
        return;
      }

      const result = data.data;
      if (result.event === "image") {
        this.img = `data:image/png;base64, ${result.image}`;
        this.signal = "";
      } else if (result.event === "alert") {
        this.danger = result.time;
      } else if (result.event === "safe") {
        this.danger = "";
      } else if (result.event === "signal") {
        this.signal = result.image;
      }
    };
  },
};
</script>
