<template>
  <div class="home container-fluid">
    <draggable
      class="row"
      v-model="cams"
      group="camera"
      @start="drag = true"
      @end="drag = false"
    >
      <CamDrag
        v-for="cam in cams"
        :key="cam.key"
        :id="cam.id"
        :data="cam.data"
        :class="cam.class"
        :style="cam.style"
        :setting_fn="setting_fn('setting', true)"
        :alarm_fn="setting_fn('alarm', true)"
      ></CamDrag>
    </draggable>

    <!-- 原本顯示每個鏡頭的程式碼 -->
    <!-- <div class="row">
      <CamDrag
        v-for="id in total"
        :key="id"
        :id="id"
        :class="col_num"
        :style="`height: calc( calc(100vh - 72px) /  ${line_count})`"
        draggable="true"
        droppable="true"
        :drag_fn="debug_print(id)"
      ></CamDrag>
    </div> -->
    <Alarm
      v-if="this.show_alarm"
      :setting_fn="setting_fn('alarm', false)"
      :cam="cam"
    ></Alarm>
    <Setting
      v-if="this.show_setting"
      :setting_fn="setting_fn('setting', false)"
      :cam="cam"
    ></Setting>
  </div>
</template>

<script>
// @ is an alias to /src
import CamDrag from "@/components/CamDrag.vue";
import Setting from "@/components/Setting.vue";
import Alarm from "@/components/Alarm.vue";
import draggable from "vuedraggable";
import { GetCameraList } from "../api/camera_list";

export default {
  name: "Home",
  components: {
    CamDrag,
    draggable,
    Setting,
    Alarm,
  },
  data() {
    return {
      total: 30, // 格子總數量
      line_num: 5, // 一排格子數量
      cams: [], // 鏡頭列表
      show_setting: false, //設定頁
      show_alarm: false, //設定頁
      cam: {},
    };
  },
  computed: {
    col_num() {
      return `${parseFloat(100 / this.line_num)}`;
      //return `col-${parseInt(12 / this.line_num)}`;
    },
    line_count() {
      let total = parseInt(this.total);
      let line_num = parseInt(this.line_num);
      let line_count = Math.ceil(total / line_num);
      return line_count;
    },
  },
  methods: {
    setting_fn(item, show) {
      let vm = this;
      return function () {
        switch (item) {
          case "alarm":
            vm.show_alarm = show;
            break;
          case "setting":
            vm.show_setting = show;
            break;
        }
        if (show) {
          vm.cam = this.data;
          // console.log("show", this);
        }

        //vm.show_setting(show);
        //console.log("this is ", this.open_setting);
      };
    },
    debug_print(id) {
      console.log("---> ", id);
      let vm = this;
      return function () {
        console.log("父元件 ", vm);
        console.log("this is ", id);
      };
    },
  },
  mounted() {
    // 先初始化好CamTag的參數值
    //GetCameraList();
    let api = GetCameraList();
    let cams = [];
    let cam_datas = [];
    api
      .then((res) => {
        if (res.status != 200) {
          window.alert(`系統異常: ${res.statusText}`);
          return;
        }
        return res.json();
      })
      .then((res) => {
        if (res.error_code !== 0) {
          window.alert(`錯誤: ${res.error_text}(${res.error_code})`);
          if (res.error_code == 8001) {
            this.$router.push("/login");
          }
          return;
        }
        cam_datas = res.data.datas;
      })
      .then(() => {
        //this.line_count = Math.ceil(cam_datas.length / line_num);

        for (let id = 0; id < cam_datas.length; id++) {
          cams.push({
            id: id,
            key: id,
            //class: this.col_num,
            style: `height: calc( calc(100vh - 72px) /  ${this.line_count});flex: 0 0 ${this.col_num}%;
    max-width: ${this.col_num}%;`,
            data: cam_datas[id],
          });
        }
        this.cams = cams;
      });
  },
};
</script>
