<template>
  <div>
    <div class="light">
      <span class="float-right pointer" @click="close"
        ><i class="fas fa-times"></i
      ></span>
      <br />
      <div class="text-left ml-2 mb-4">
        <h5>二次驗證</h5>
      </div>
      <div class="ml-3">
        <span class="enter-label text-right">User ID</span>
        <input
          class="border rounded ml-4 enter"
          v-model="username"
          placeholder="your username"
        />
        <br /><br />
        <span class="enter-label text-right">Password</span>
        <input
          class="border rounded ml-2 enter"
          v-model="password"
          placeholder="your password"
          @keypress.enter="confirm"
          type="password"
        />
        <br />
        <span
          v-if="hint != ''"
          class="bg-gradient-danger text-danger text-left"
        >
          {{ hint }}
        </span>
        <br />
        <div class="w-100 text-right pr-4">
          <button
            type="button"
            class="btn btn-outline-secondary"
            @click="close()"
          >
            取消
          </button>
          <button type="button" class="btn btn-warning" @click="confirm()">
            確認
          </button>
        </div>
      </div>
    </div>

    <div class="fades"></div>
  </div>
</template>

<script>
// @ is an alias to /src
// import { Login } from "../api/auth";
export default {
  name: "Confirm",
  data() {
    return {
      username: "",
      password: "",
      hint: "",
    };
  },
  watch: {
    username(v) {
      this.hint = "";
      this.username = v.trim();
    },
    password(v) {
      this.hint = "";
      this.password = v.trim();
    },
  },
  props: {
    callback_fn: {
      type: Function,
      default: function () {},
    },
    close_fn: {
      type: Function,
      default: function () {},
    },
    cam: {},
  },
  computed: {},
  methods: {
    close() {
      this.close_fn();
    },
    confirm() {
      let missing = [];
      if (this.username === "") {
        missing.push("帳號");
      }

      if (this.password === "") {
        missing.push("密碼");
      }

      if (missing.length > 0) {
        this.hint = "請輸入" + missing.join(", ");
        return;
      }

      this.callback_fn(this.username, this.password);
    },
  },
};
</script>
