<template>
  <div id="app">
    <nav
      class="navbar navbar-expand-lg navbar-light bg-light border-bottom"
      v-if="showNav"
    >
      <router-link class="navbar-brand" to="/">
        <div class="backgroundLogo"></div>
      </router-link>
      <span class="d-none">Welcome, {{ username }}</span>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto d-none">
          <li class="nav-item">
            <router-link class="nav-link" to="/">Home</router-link>
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/login">Login</router-link>
          </li>
        </ul>
      </div>
    </nav>

    <router-view v-if="canShow" />
  </div>
</template>

<script>
import { AuthUser } from "./api/auth";

export default {
  data: () => ({
    username: "",
    has_login: false,
  }),
  computed: {
    showNav() {
      return this.$route && this.$route.name != "Login";
    },
    canShow() {
      return !this.showNav || this.has_login;
    },
  },
  watch: {
    $route() {
      // 網址改變則標示為還沒驗證
      this.checkLogin();
    },
  },
  methods: {
    checkLogin() {
      // 取user資料，已經登入就轉導
      AuthUser()
        .then((res) => {
          if (res.status != 200) {
            window.alert(`系統異常: ${res.statusText}`);
            return;
          }
          return res.json();
        })
        .then((res) => {
          if (res.error_code !== 0) {
            if (this.$route && this.$route.path !== "/login") {
              this.$router.push("/login"); // 轉導
              if (res.error_code === 8000) {
                window.alert(`No Permission. (${res.error_code})`);
              } else if (res.error_code === 8001) {
                window.alert(`Please login first. (${res.error_code})`);
              } else {
                window.alert(
                  `System Error: ${res.error_text}(${res.error_code})`
                );
              }
            }
            return;
          }

          if (res.data != null) {
            this.username = res.data;
            if (this.$route && this.$route.path !== "/") {
              this.$router.push("/"); // 轉導
            }
            this.has_login = true;
          }
        });
    },
  },
  mounted() {
    this.checkLogin();
  },
};
</script>

<style lang="scss">
.backgroundLogo {
  background-image: url(./assets/logo_normal.png);
  background-repeat: no-repeat;
  background-position: center;
  height: 45px !important;
  width: 230px !important;
  background-size: cover;
}
</style>
