export function CamDetailSetting(cam_id, description) {
    return fetch(`/api/cam/camera/single/${cam_id}`, {
        method: "put",
        body: JSON.stringify({
            description,
        }),
        headers: {
            "Content-Type": "application/json",
        },
    })
}

// 開關電子圍籬偵測
export function CamUpdateFenceDetect(cam_id, enable, username, password) {
    return fetch(`/api/cam/camera/${cam_id}/fence-enable`, {
        method: 'put',
        body: JSON.stringify({
            username,
            password,
            enable
        }),
        headers: {
            "Content-Type": "application/json",
        },
    })
}


// 開關電子圍籬偵測
export function CamInfo(cam_id) {
    return fetch(`/api/cam/camera/${cam_id}`)
}
