<template>
  <div>
    <div class="light">
      <span class="float-right pointer" @click="close"
        ><i class="fas fa-times"></i
      ></span>
      <br />
      <div class="text-left ml-2 mb-4">
        <h5>關閉警報</h5>
      </div>
      <div class="ml-3">
        <span class="enter-label text-right">User ID</span
        ><input
          class="border rounded ml-4 enter"
          v-model="username"
          placeholder="your username"
        /><br /><br />
        <span class="enter-label text-right">Password</span
        ><input
          class="border rounded ml-2 enter"
          v-model="password"
          @keypress.enter="close_alarm"
          type="password"
        /><br />
        <div class="w-100 text-right pr-4">
          <span class="text-right forget mt-2 d-none">Forget Password</span>
        </div>
        <br />
        <div class="w-100 text-right pr-4">
          <span class="button pointer" @click="close_alarm()"
            ><strong>Login</strong></span
          >
        </div>
      </div>
    </div>

    <div class="fades"></div>
  </div>
</template>

<script>
// @ is an alias to /src
import { Login } from "@/api/auth";
export default {
  name: "Alarm",
  data() {
    return {
      username: "",
      password: "",
    };
  },
  watch: {
    username(v) {
      this.username = v.trim();
    },
    password(v) {
      this.password = v.trim();
    },
  },
  props: {
    setting_fn: {
      type: Function,
      default: function () {},
    },
    cam: {},
  },
  computed: {},
  methods: {
    close() {
      this.setting_fn();
    },
    close_alarm() {
      this.ws = new WebSocket(
        `ws://${window.location.host}/ws/camera/streaming/${this.cam.camera_id}`
      );
      this.ws.onopen = () => {
        console.log(this.connecting);

        this.close_ws = () => this.ws.close();
        this.send_ws = (payload) => this.ws.send(payload);
      };
      console.log("test");
      let api = Login(this.username, this.password);
      api
        .then((res) => {
          if (res.status != 200) {
            window.alert(`系統異常: ${res.statusText}`);
            return;
          }
          return res.json();
        })
        .then((res) => {
          console.log(res);
          if (res.error_code !== 0) {
            window.alert(`帳號密碼錯誤: ${res.error_text}(${res.error_code})`);
            return;
          }

          this.send_ws(
            JSON.stringify({
              event: "safe",
            })
          );
          this.close_ws();
          this.close();
          //this.setting_fn("alarm", false);
        });
    },
  },
  mounted() {
    //this.connect();
  },
};
</script>
