export function FenceSetting(cam_id, points) {
    return fetch("/api/cam/fence/" + cam_id, {
        method: "put",
        body: JSON.stringify({
            points,
        }),
        headers: {
            "Content-Type": "application/json",
        },
    })
}
export function GetFenceDetail(cam_id) {
    return fetch(`/api/cam/fence/` + cam_id)
}