<template>
  <div>
    <div class="light">
      <span class="float-right pointer" @click="close"
        ><i class="fas fa-times"></i
      ></span>
      <br />
      <div class="text-left ml-2 mb-4">
        <h5>電子圍籬設定</h5>
      </div>
      <div>
        <div class="input-group mb-2">
          <span class="input-group-text">描述</span>
          <textarea
            class="form-control"
            aria-label="With textarea"
            v-model="description"
          ></textarea>
          <span class="btn btn-danger clear" @click="clear_points(640, 480)">
            圍籬
            <i class="fas fa-eraser"></i>
          </span>
        </div>
        <div class="w-100 d-flex justify-content-center">
          <span
            v-if="img == ''"
            class="w3-padding w3-display-middle text-danger"
          >
            連線失敗
            <i class="fa fa-spinner w3-spin"></i>
          </span>
          <canvas
            :class="img != '' ? 'border' : 'border d-none'"
            @click="add_point"
            :style="img"
            ref="canvasElement"
            :width="width"
            :height="height"
          >
          </canvas>
        </div>
        <div v-if="img != ''" class="w-100 text-center mt-2">
          <span class="btn btn-primary" @click="setting_send">設定</span>
        </div>
      </div>
    </div>

    <div class="fades"></div>
  </div>
</template>

<script>
// @ is an alias to /src
import { FenceSetting, GetFenceDetail } from "../api/fence";
import { CamDetailSetting } from "../api/camera_detail";
export default {
  name: "Setting",
  data() {
    return {
      description: "",
      points: [],
      ctx: {},
      ws: WebSocket,
      img: "",
      width: 640,
      height: 480,
      finish_setting: false,
    };
  },
  props: {
    setting_fn: {
      type: Function,
      default: function () {},
    },
    cam: {},
  },
  computed: {},
  methods: {
    close() {
      this.ws.close();
      this.setting_fn("setting", false);
    },
    setting_send() {
      if (this.finish_setting) {
        let api = FenceSetting(this.cam.camera_id, this.points);

        api
          .then((res) => {
            if (res.status != 200) {
              window.alert(`系統異常: ${res.statusText}`);
              return;
            }
            return res.json();
          })
          .then((res) => {
            console.log(res);
            if (res.error_code !== 0) {
              window.alert(`設定錯誤: ${res.error_text}(${res.error_code})`);
              return;
            }
            CamDetailSetting(this.cam.camera_id, this.description);

            window.alert(`設定成功！！`);
            this.cam.description = this.description;
            this.close();
          });
      } else {
        let api = CamDetailSetting(this.cam.camera_id, this.description);
        api
          .then((res) => {
            if (res.status != 200) {
              window.alert(`系統異常: ${res.statusText}`);
              return;
            }
            return res.json();
          })
          .then((res) => {
            console.log(res);
            if (res.error_code !== 0) {
              window.alert(`設定錯誤: ${res.error_text}(${res.error_code})`);
              return;
            }

            window.alert(`設定成功！！`);
            this.cam.description = this.description;
            this.close();
          });
      }
    },
    getEventPosition(ev) {
      var x, y;
      if (ev.layerX || ev.layerX == 0) {
        x = ev.layerX;
        y = ev.layerY;
      } else if (ev.offsetX || ev.offsetX == 0) {
        // Opera
        x = ev.offsetX;
        y = ev.offsetY;
      }
      return { x: x, y: y };
    },
    set_canvas() {
      let canvas = this.$refs["canvasElement"];
      let context = canvas.getContext("2d");
      this.ctx = context;
    },
    clear_points(width, height) {
      this.points = [];
      this.finish_setting = false;
      this.ctx.clearRect(0, 0, width, height);
    },
    segmentsIntr(a, b, c, d) {
      /** 1 解線性方程組, 求線段交點. **/
      // 如果分母為0 則平行或共線, 不相交
      var denominator = (b.y - a.y) * (d.x - c.x) - (a.x - b.x) * (c.y - d.y);
      if (denominator == 0) {
        return false;
      }
      // 線段所在直線的交點座標 (x , y)
      var x =
        ((b.x - a.x) *
          (d.x - c.x) *
          (c.y - a.y)(b.y - a.y) *
          (d.x - c.x) *
          a.x -
          (d.y - c.y) * (b.x - a.x) * c.x) /
        denominator;
      var y =
        -(
          (b.y - a.y) *
            (d.y - c.y) *
            (c.x - a.x)(b.x - a.x) *
            (d.y - c.y) *
            a.y -
          (d.x - c.x) * (b.y - a.y) * c.y
        ) / denominator;
      /** 2 判斷交點是否在兩條線段上 **/
      if (
        // 交點線上段1上
        (x - a.x) * (x - b.x) <= 0 &&
        (y - a.y) * (y - b.y) <= 0 &&
        // 且交點也線上段2上
        (x - c.x) * (x - d.x) <= 0 &&
        (y - c.y) * (y - d.y) <= 0
      ) {
        // 返回交點p
        return {
          x: x,
          y: y,
        };
      }
      //否則不相交
      return false;
    },
    twoPointDistance(p1, p2) {
      let dep = Math.sqrt(Math.pow(p1.x - p2.x, 2) + Math.pow(p1.y - p2.y, 2));
      return dep;
    },
    draw() {
      for (let i = 0; i < this.points.length; i++) {
        if (i + 1 < this.points.length) {
          this.ctx.moveTo(this.points[i].x, this.points[i].y);
          this.ctx.lineTo(this.points[i + 1].x, this.points[i + 1].y);
          this.ctx.strokeStyle = "yellow";
          this.ctx.lineWidth = 3;
          this.ctx.stroke();
        }
        this.ctx.strokeStyle = "red";
        this.ctx.fillStyle = "red";
        this.ctx.beginPath();
        this.ctx.arc(
          this.points[i].x,
          this.points[i].y,
          5,
          0,
          Math.PI * 2,
          false
        );
        this.ctx.closePath();
        this.ctx.stroke();
        this.ctx.fill();
      }
    },
    add_point(e) {
      let p = this.getEventPosition(e);
      if (!this.finish_setting) {
        if (this.points.length > 0) {
          //console.log(this.twoPointDistance(this.points[0], p));
          if (this.twoPointDistance(this.points[0], p) <= 5) {
            this.finish_setting = true;
            this.points.push(this.points[0]);
            this.draw();
            console.log(this.points);
            return;
          }
        }
        this.points.push(p);
        //segmentsIntr();
        this.draw();
      } else {
        window.alert("已完成設定");
      }
    },
    connect() {
      this.ws = new WebSocket(
        `ws://${window.location.host}/ws/camera/streaming/${this.cam.camera_id}`
      );
      this.ws.onopen = () => {
        //console.log(this.connecting);
        this.close_ws = () => this.ws.close();
        this.send_ws = (payload) => this.ws.send(payload);
      };
      this.ws.onmessage = (e) => {
        const data = JSON.parse(e.data);
        if (data.error_code !== 0) {
          //this.warning = data.error_text;
          return;
        }

        const result = data.data;
        if (result.event === "image") {
          //this.img = result.image;
          //var img_data = btoa(result.image);

          //console.log(type(img_data));
          var newImg = new Image();
          newImg.src = `data:image/png;base64,${result.image}`;
          this.height = newImg.height != 0 ? newImg.height : this.height;
          this.width = newImg.width != 0 ? newImg.width : this.width;
          //console.log(`reSize--->(${newImg.width},${newImg.height})`);

          this.img = `background: url('data:image/png;base64,${result.image}') center center /cover`;
          //this.signal = "";
        }
      };

      let api = GetFenceDetail(this.cam.camera_id);
      api
        .then((res) => {
          if (res.status != 200) {
            window.alert(`系統異常: ${res.statusText}`);
            return;
          }
          return res.json();
        })
        .then((res) => {
          console.log(res);
          if (res.error_code !== 0) {
            if (res.error_code != 6000) {
              window.alert(`設定錯誤: ${res.error_text}(${res.error_code})`);
            }
            return;
          }
          this.points = res.data.points;
          this.draw();
        });
    },
  },
  mounted() {
    this.set_canvas();
    this.connect();
    this.description = this.cam.description;
    //  this.ws = new WebSocket(
    //   `ws://127.0.0.1:8000/ws/camera/streaming/${this.data.camera_id}`
    // );
  },
};
</script>
<style lang="scss" scoped>
.clear {
  display: flex;
  align-items: center;
}
.w-80 {
  width: 80%;
}
.fades {
  display: block;
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background-color: black;
  z-index: 1001;
  -moz-opacity: 0.8;
  opacity: 0.8;
  filter: alpha(opacity=80);
}
.light {
  display: block;
  position: absolute;
  top: calc(calc(100% -550px) / 10);
  left: 15%;
  width: 70%;
  height: 550px;
  padding: 16px;
  //border:3px solid orange;
  background-color: white;
  z-index: 1002;
  border-radius: 0.6rem !important;
  overflow: auto;
}
.pointer {
  cursor: pointer;
}
</style>